body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
  color: #333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1, h2, h3 {
  color: #333;
  margin-bottom: 0.5em;
}

a {
  text-decoration: none;
  color: #007bff;
}

a:hover {
  text-decoration: underline;
}

/* Layout & Containers */
.container {
  margin: 0 auto;
  padding: 20px;
  max-width: 1200px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

/* Tables */
table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}

th, td {
  padding: 12px 15px;
  border: 1px solid #ddd;
  text-align: center;
}

th {
  background-color: #007bff;
  color: white;
  font-weight: bold;
}

td {
  background-color: #f9f9f9;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* Responsive Tables */
@media (max-width: 768px) {
  table {
    font-size: 0.8em;
  }
}

/* Chart Styling */
.chart-container {
  margin: 40px 0;
}

canvas {
  max-width: 100%;
}

/* Buttons */
button {
  background-color: #007bff;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background-color: #0056b3;
}

/* Inputs */
input {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  max-width: 300px;
}

input:focus {
  border-color: #007bff;
  outline: none;
}

/* Footer */
footer {
  text-align: center;
  padding: 20px;
  margin-top: 20px;
  background-color: #333;
  color: white;
  font-size: 0.9em;
}

footer a {
  color: #007bff;
}

footer a:hover {
  color: #0056b3;
}

/* General Spacing */
.margin-top {
  margin-top: 20px;
}

.margin-bottom {
  margin-bottom: 20px;
}

.financial-table {
  width: 100%;
  border-collapse: collapse;
}

.financial-table th, .financial-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.financial-table th {
  background-color: #f2f2f2;
  color: #333;
}

.hamburger-menu {
  position: relative;
}

.hamburger-icon {
  font-size: 2rem;
  cursor: pointer;
  color: #333;
  position: fixed;
  top: 20px;
  right: 20px;
}

.menu-items {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 60px;
  right: 20px;
  background-color: #fff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 1rem;
  border-radius: 5px;
}

.menu-items a {
  margin: 0.5rem 0;
  text-decoration: none;
  color: #007bff;
}

.menu-items a:hover {
  text-decoration: underline;
}